body {
    font-family: "Avenir Next";
}

.main-title {
    text-align: center;
    text-transform: uppercase;
}

li {
    list-style: none;
}

.title {
    text-transform: uppercase;
    font-weight: 600;
}

.subtitle {
    display: block;
    text-align: center;
    font-size: 20px;
    font-weight: 600;
    margin-bottom: 1rem;
}

.comment {
    border: 1px solid #808080;
    color: #707070;
    padding: 1rem;
    margin-bottom: 1rem;
}

.post {
    padding: 2rem;
    border: 1px solid #808080;
    margin-bottom: 2rem;
}

.input-wrapper {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: center;
}

.label {
    display: block;
    margin-bottom: 0.5rem;
}

.input {
    margin: 0 1rem;
    outline: none;
}

.button {
    padding: 1rem 1.5rem;
    font-size: 18px;
    display: block;
    margin: 0 auto;
}

